*{
    box-sizing: border-box;
    margin: 0; padding: 0;
}
*::before, *::after{
    box-sizing: border-box;
}
/*屏幕宽度小于1024上下布局*/
@media screen and (max-width: 1024px) {
    main{
        display:flex;
        flex-direction: column;
    }
    #codeDisplay{
        padding-left:80px;
        padding-bottom: 40px;
    }
}
/*屏幕宽度大于1024左右布局*/
@media screen and (min-width: 1025px) {
    main{
        display:flex;
        align-items: flex-start;
        height: 100vh;
    }
    #codeDisplay{
        padding:5px;
    }
}
.content{
    text-align: center;
    background-color:gray;
}
#codeDisplay{
    overflow-y: auto;
}
/*隐藏滚动条*/
#codeDisplay::-webkit-scrollbar{
    display: none;
}
#demo{
    display: none;
}
.tools{
    display:flex;
    align-items: flex-start;
    justify-content: space-between;
}
#buttons{
    display:flex;
    flex-direction: column;
}
#buttons > button {
    margin: 5px;
    width: 80px;
    height: 40px;
    border-radius: 10px;
}
#btnFast{
    background-color: #4cad38;
}
main{
    position:fixed;
    top: 0;
    left:0;
}

